.loginBlock {
  box-sizing: border-box;
  padding: 25px 33px;

  width: 413px;
  height: 600px;

  background-color: #F9F9FE;

  position: absolute;
  left: 50%;
  top: 54%;

  transform: translate(-50%, -50%);

  .logo{
    margin-bottom: 32px;

    font-weight: 600;
    font-size: 26px;
    line-height: 39px;


  }
  .title{
    margin-bottom: 40px;

    font-weight: 600;
    font-size: 22px;
    line-height: 33px;
  }
  .emailBlock{
    margin-bottom: 30px;

    text-align: left;

    & span{
      font-size: 13px;
      line-height: 20px;

      opacity: 0.7;
    }


    .inputText{
      width: 320px;

      margin: 0;

      padding: 10px 0 5px 0;

      background-color: inherit;

      border-top:0;
      border-left: 0;
      border-right: 0;
      border-bottom: 2px solid #24254A;

      box-shadow: none;

      color:black;

      font-size: 16px;
    }
  }
  .passwordBlock{
    margin-bottom: 30px;

    text-align: left;

    & span{
      font-size: 13px;
      line-height: 20px;

      opacity: 0.7;
    }
    .inputPassword{
      width: 320px;

      margin: 0;

      padding: 10px 0 5px 0;

      background-color: inherit;

      border-top:0;
      border-left: 0;
      border-right: 0;
      border-bottom: 2px solid #24254A;

      box-shadow: none;

      color:black;
      font-size: 16px;

      outline: none;
    }
  }
  .link{
    margin-bottom: 50px;

    display: block;

    font-size: 14px;
    line-height: 17px;

    transform: translateX(27%);

    color:black;

    text-decoration: none;

    transition: all .2s ease-in-out;

    &:active{
      color:goldenrod;
    }
  }
  .loginBtn{
    margin-bottom: 20px;
    width: 266px;
    height: 36px;

    background-color: #9890c7;

    border-radius: 20px;

    color:white;
    font-size: 14px;

    border-style: none;

    transition: all .2s ease-in-out;

    &:active{
      background-color: goldenrod;
    }
  }
  .redirectBlock{
    margin-top: 20px;

    display: flex;
    flex-direction: column;
    gap: 10px;
    & span{
      font-size: 13px;
      line-height: 20px;

      opacity: 0.7;
    }
    .signBtn{


      background-color: inherit;
      border-style: none;

      transition: all .2s ease-in-out;

      &:active{
        color:goldenrod;
      }
    }
  }
}